export function isPolymerElementUnresolved(node) {
  const isCustomElement = node.tagName.indexOf('-') > -1 ;
  const isPolymer1 = node.__isPolymerInstance__ === true;
  const isPolymer2 = (
    (Polymer.Element !== undefined && node instanceof Polymer.Element) ||
    (Polymer.ElementMixin !== undefined && node instanceof Polymer.ElementMixin) ||
    (Polymer.LegacyElementMixin !== undefined && node instanceof Polymer.LegacyElementMixin) ||
    (node.is !== undefined && node.ready !== undefined && node.notifyPath !== undefined)
  );

  return isCustomElement && !isPolymer1 && !isPolymer2;
}

export default {
  isPolymerElementUnresolved
};



