import CellsBridgeManagerDom from './manager/dom';

export default class CellsBridgeComponent extends CellsBridgeManagerDom {
  /**
   * Componen type. It could be TYPE_UI, TYPE_DM or TYPE_CC.
   *
   * @type {String}
   */
  type = '';

  spec = null;
  zone = null;
  node = null;
  fixed = null;
  priority = null;
  connections = null;

  constructor(spec) {
    super();
    // @TODO: Por qué almacenar spec en una propiedad y algunas de sus propiedades en otra.
    this.spec = spec;
    this.type = spec.type;
    this.zone = spec.zone;
    this.fixed = spec.fixed;
    this.priority = spec.priority;
    this.node = this.spec.node ? this.spec.node : this.createElement(this.spec.tagName); // NOT WORKING CellsBridgeComponent
    this.connections = spec.connections;
  }

  setProps() {
    var spec = this.spec;
    if (spec.connections && spec.connections.ignoreAttr) {
      for (let index = 0; index < spec.connections.ignoreAttr.length; index++) {
        const key = spec.connections.ignoreAttr[index];
        delete spec.properties[key];
      }
    }

    this.setAttrs(this.node, spec.properties);
  }

  isUnresolved() {
    return this.node.tagName.indexOf('-') > -1 && this.node.constructor === HTMLElement;
  }
}



