import eventManager from './manager/events'
import { Subscription } from 'rxjs/Subscription';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/observable/fromEvent';
import { ReplaySubject } from 'rxjs/ReplaySubject';
import { ObjectUnsubscribedError } from 'rxjs/util/ObjectUnsubscribedError';
import Constants from './constants';

const { externalEventsCodes } = Constants;


/**
 * Channel ReplaySubject extension of Rx.ReplaySubject.
 * This is pretty hacky, but so far I'd found no better way of having
 * Channels that do no close on multicasted stream completion and on multiple errors.
 * For documentation refer to
 * [Rx.ReplaySubject docs](@link https://github.com/Reactive-Extensions/RxJS/blob/master/doc/api/subjects/replaysubject.md).
 * The only difference is that Channel never triggers '.onCompleted()' and
 * does not closes observers on errors (thus allowing to continuously dispatch them).
 */
var Channel = function(buffer) {
  ReplaySubject.call(this, buffer);
}

Channel.prototype = Object.create(ReplaySubject.prototype);

Channel.prototype.hasObservers = function hasObservers() {
  if (this.closed) {
    throw new ObjectUnsubscribedError();
  }

  return this.observers.length > 0;
};

Channel.prototype.next = function next(value) {
  let generateUUID = function() {
    let d = new Date().getTime();
    let uuid = 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, function(c) {
      let r = (d + Math.random()*16)%16 | 0;
      return (c=='x' ? r : (r&0x3|0x8)).toString(16);
    });
    return uuid;
  };
  if (!value.uuid) {
    value.uuid = generateUUID();
  }
  ReplaySubject.prototype.next.call(this, value);
};

Object.assign(Channel.prototype, {
  /**
   * Dummy method override to prevent execution and Rx.Observable completion
   * @ignore
   */
  complete: function() {},

  /**
   * Dummy method override to prevent stop execution when error in Rx.Observable
   * @ignore
   */
  error: function(error) {
    this.error = error;
    this.observers.forEach(function(o) {
      o.isStopped = false;
      o.onError(error);
    });
  },

  /**
   * Removes a specific observer.
   *
   * @param  {Number} index  Position of observer in array
   * @example
   * channel.unsubscribe(1);
   */
  unsubscribe: function(index) {
    this.observers.splice(index, 1);
  },

  /**
   * Removes all observers.
   *
   * @example
   * channel.unsubscribe();
   */
  unsubscribeAll: function() {
    this.observers.splice(0, this.observers.length);
  },

  /**
   * Resets the channel.
   */
  clean: function() {
    this._events = [];
    this.observers = [];
  },

  dispose: function() {
    ReplaySubject.prototype.unsubscribe.call(this);
  }
});


/**
 * A subscriptor can publish to a channel and receive notifications
 * about changes from a channel.
 *
 * @param {HTMLElement/Object} node
 */
var Subscriptor = function(node) {
  this.node = node;
  this.subscriptions = []; //[] //*1prototype mixin clone problem
  this.publications = new Subscription();
};

Subscriptor.prototype = {
  node: null,
  subscriptions: null, //[] //*1prototype mixin clone problem
  publications: null,

  /**
   * Returns true if the subscriptor has already subscribed to a channel.
   *
   * @param  {Channel}  channel The channel to check the subscription
   * @return {Boolean}
   */
  hasSubscription: function(channel) {
    return(this.subscriptions.filter(d => d.channel === channel).length > 0);
  },

  /**
   * Publish an event.sss
   *
   * @param  {Event} event
   */
  publish: function(event) {
    this.publications.add(event);
  },

  makeCallbackWithNoReplay: function(channel, fn) {
    if (!fn.node.intervals) {
      fn.node.intervals = [];
    }
    if (!fn.node.ids) {
      fn.node.ids = [];
    }
    let getTimeFromNode = function(node) {
      return node.intervals[channel.name];
    };
    let setTimeForNode = function(node, time) {
      node.intervals[channel.name] = time;
    };
    let getTimeFromChannel = function(channel) {
      return channel._events.length > 0 ? channel._events[0].time : 1;
    };
    let getIdFromNode = function(node) {
      return node.ids[channel.name];
    };
    let setIdForNode = function(node, id) {
      node.ids[channel.name] = id;
    };
    let getIdFromChannel = function(channel) {
      return channel._events.length > 0 ? channel._events[0].value.uuid : null;
    };
    let fnReplayOff = function() {
      let lastInterval = getTimeFromChannel(channel);
      let nodeInterval = getTimeFromNode(fn.node);
      let lastId = getIdFromChannel(channel);
      let nodeId = getIdFromNode(fn.node);
      if (!nodeInterval || nodeInterval < lastInterval ||
        (nodeInterval === lastInterval && (nodeId != lastId))) {
        setTimeForNode(fn.node, lastInterval);
        setIdForNode(fn.node, lastId);
        return fn.apply(this, arguments);
      }
      setTimeForNode(fn.node, lastInterval);
    }.bind(this);
    fnReplayOff.node = fn.node;
    return fnReplayOff;
  },

  /**
   * Subscribe to a channel.
   *
   * @param  {Channel}   channel  Channel to subscribe
   * @param  {Function}  fn       Callback function to run when a value from a channel changed
   */
  subscribe: function(channel, fn, previousState) {
    if(!this.hasSubscription(channel)) {
      let callback = fn;
      let subscription;
      if (previousState === false) {
        callback = this.makeCallbackWithNoReplay(channel, fn);
      }

      let pos = this._firstInstanceOfObserver(callback.node, channel);
      if(pos === -1) {
        subscription = channel.subscribe(callback);
        pos = channel.observers.length - 1;
      }
      channel.observers[pos].node = callback.node;

      const subscriptionDetail = {
        channel: channel,
        observer: channel.observers[pos],
        subscription: subscription
      };

      this.subscriptions.push(subscriptionDetail);
    }
  },

  /**
   * Remove all active subscriptions.
   */
  unsubscribe: function() {
    for(var i = this.subscriptions.length - 1; i >= 0; i--) {
      var subscription = this.subscriptions[i];
      var channel = subscription.channel;
      var observer = subscription.observer;
      var index = channel.observers.indexOf(observer);

      //if(index >= 0 && !channel.name.match(/\b__bridge_/)) {
      if(index >= 0) {
        channel.unsubscribe(index);
      }
    }

    this.publications.unsubscribe();
  },

  /**
   * Returns the position of the first occurrence of the observer's node in the channel.
   * If the node has none observer registered to the channel, it returns -1.
   *
   * @param {node}
   * @param {channel}
   */
  _firstInstanceOfObserver: function(node, channel) {
    var pos = -1;

    for(let index = 0; index < channel.observers.length; index++) {
      if(channel.observers[index].node === node) {
        pos = index;
        break;
      }
    }
    return pos;
  }
};

/**
 * Collection of channels.
 */
var ChannelManager = function() {
  this.channels = {}; //*1prototype mixin clone problem
};

ChannelManager.prototype = {
  channels: null, //{} //*1prototype mixin clone problem

  /**
   * Gets a channel by name.
   *
   * @param  {String} name
   *
   * @return {Channel}
   */
  get: function(name) {
    var channel = this.channels[name];

    if(channel == null) {
      channel = this.create(name);
    }

    return channel;
  },

  /**
   * Gets a channel by name.
   *
   * @param  {String} name
   *
   * @return {Channel}
   */
  getUnsafe: function(name) {
    return this.channels[name];
  },

  /**
   * Creates a channel.
   *
   * @param  {String} name Channel name
   *
   * @return {Channel}
   */
  create: function(name) {
    var channel = new Channel(1);
    channel.name = name;
    return this.channels[name] = channel;
  },

  /**
   * Removes a channel from the collection.
   *
   * @param  {String} name Name of the channel to remove.
   */
  remove: function(name) {
    delete this.channels[name];
  },


  unregisterAllSubscriptors() {
    this.ComponentConnector.subscriptors.forEach((v) => {
      v.subscriptions.forEach(s => s.subscription.unsubscribe());
    });
  },

  cleanAllChannels: function() {
    for (let channelName in this.channels) {
      this.channels[channelName].clean();
      //this.channels[channelName].dispose();
    }
  },

  removeAllChannels: function() {
    for (let channelName in this.channels) {
      delete this.channels[channelName];
    }
  },

  /**
   * Returns true if there is a channel registered with the given name,
   *
   * @param {String} name Channel name,
   *
   * @return {Boolean}
   */
  has: function(name) {
    return this.channels[name] != null;
  }
};


let ComponentConnector = class ComponentConnector {
  manager = null;
  subscriptors = null;
  bridgeChannelsPrefix = null;

  constructor() {
    this.manager = new ChannelManager();
    this.subscriptors = new Map();
    this.bridgeChannelsPrefix = /__bridge_(?!ch)/;
  }

  getSubscriptor(node) {
    var subscriptor = this.subscriptors.get(node);

    if(!subscriptor) {
      subscriptor = new Subscriptor(node);
      this.subscriptors.set(node, subscriptor);
    }

    return subscriptor;
  }

  /**
   * Register a node in pubsub
   *
   * @param  {HTMLElement}  node
   * @param  {Object}       connections
   */
  register(node, connections) {
    if(node && connections) {
      this._registerOutConnections(node, connections.out);
      this._registerInConnections(node, connections.in);
    }
  }

  /**
   * Registesr new connections of a node that may have other connections registered previously.
   *
   * @param  {HTMLElement}  node
   * @param  {Object}       connections
   */
  progressiveRegister(node, connections) {
    if(node && connections) {
      this._registerOutConnections(node, connections.out);
      this._updateInConnections(node, connections.in);
    }
  }

  _registerInConnections(node, inConnections) {
    let channelName;
    let bindName;
    let previousState;

    if(inConnections) {
      for(channelName in inConnections) {
        bindName = inConnections[channelName].bind;
        previousState = inConnections[channelName].previousState || false;
        this.addSubscription(channelName, node, bindName, previousState);
      }
    }
  }

  addSubscription(channelName, node, bind, previousState = false) {
    const callback = this._wrapCallbackWithNode(node, bind);
    const channel = this.manager.get(channelName); // @TODO ojo!!! CC this.isBridgeChannel(channelName) ? this.manager.getUnsafe(channelName) : this.manager.get(channelName);

    if(channel) {
      const subscriptor = this.getSubscriptor(node);

      subscriptor.subscribe(channel, callback, previousState);
    }
  }

  _updateInConnections(node, inConnections) {
    let channelName;
    let bindName;
    let previousState;

    if(inConnections) {
      for(channelName in inConnections) {
        bindName = inConnections[channelName].bind;
        previousState = inConnections[channelName].previousState || false;
        this.updateSubscription(channelName, node, bindName, previousState);
      }
    }
  }

  updateSubscription(channelName, node, bind, previousState = false) {
    const subscriptor = this.getSubscriptor(node);

    if(this.isActiveBridgeChannel(channelName) ||
      (!this.isActiveBridgeChannel(channelName) && !this.hasSubscriptions(subscriptor, channelName))
    ) {
      const channel = this.manager.get(channelName);
      const callback = this._wrapCallbackWithNode(node, bind);

      subscriptor.subscribe(channel, callback, previousState);
    }
  }

  _wrapCallbackWithNode(node, bindName) {
    let cb = this.wrapCallback(node, bindName);
    cb.node = node;
    return cb;
  }

  _hasPublisher(subscriptor, node, channelName, bindName) {
    let hasPublisher = false;

    if (subscriptor.publications._subscriptions) {
      subscriptor.publications._subscriptions.forEach(function(publication){
        if (publication.node === node &&
        publication.channelName === channelName &&
        publication.eventName === bindName) {
          hasPublisher = true;
        }
      });
    }
    return hasPublisher;
  }

  _registerOutConnections(node, outConnections) {
    let channelName;
    let bindName;

    if(outConnections) {
      for(channelName in outConnections) {
        bindName = outConnections[channelName].bind;

        this.addPublication(channelName, node, bindName, outConnections[channelName]);
      }
    }
  }

  addPublication(channelName, node, bindName, outConnectionDefinition) {
    if(this.isBridgeChannel(channelName)) {
      console.warn(`Forbidden operation. You are trying to write to a private channel (${channelName}).`);
    } else {
      let subscriptor = this.getSubscriptor(node);
      let channel = this.manager.get(channelName);
      let hasPublisher = this._hasPublisher(subscriptor, node, channelName, bindName);

      if (!hasPublisher) {
        subscriptor.publish(this.wrapEvent(node, bindName, channel, outConnectionDefinition));
      }
    }
  }

  publish(channelName, value) {
    if(this.isBridgeChannel(channelName)) {
      console.warn(`Forbidden operation. You are trying to write to a private channel (${channelName}).`);
    } else {
      const channel = this.manager.get(channelName);
      const customEventName = `${channelName}-publish`;

      channel.next(new CustomEvent(customEventName, { detail: value }));
    }
  }

  /**
   * Unregister a node from pubsub
   *
   * @param  {HTMLElement} node
   */
  unregister(node) {
    if(!node) {
      return;
    }

    var subscriptor = this.subscriptors.get(node);

    if(subscriptor) {
      subscriptor.unsubscribe();
      this.subscriptors.delete(node);
    }
  }

  unregisterAllSubscriptors() {
    this.subscriptors.forEach((v) => {
      v.subscriptions.forEach(s => s.subscription.unsubscribe());
    });
  }

  // @TODO unificar con unregister
  sleep(node) {
    if(!node) {
      return;
    }

    var subscriptor = this.subscriptors.get(node);

    if(subscriptor) {
      console.log(subscriptor);
      //subscriptor.unsubscribe();
    }
  }

  /**
   * Wrap a callback.
   *
   * @param  {HTMLElement/Object} node
   * @param  {String} bindName Method or property name
   *
   * @return {Function}
   */
  wrapCallback(node, bindName) {
    var wrappedCb = function(event) {
      if (typeof(bindName) === 'function') {
        bindName(event.detail);
      } else {
        if(typeof node[bindName] === 'function') {
          node[bindName](event.detail);
        } else {
          node[bindName] = event.detail;
        }
      }
    };

    return wrappedCb;
  }

  /**
   * Returns true if the event has reached the node that is listening the event
   *
   * @param {Event} event
   */
  _isEventAtTarget(event) {
    const AT_TARGET = Event.AT_TARGET || Event.prototype.AT_TARGET;
    return event.eventPhase === AT_TARGET;
  }

  /**
   * Wrap an event.
   *
   * @param  {HTMLElement} node
   * @param  {String} eventName
   * @param  {Channel} channel
   *
   * @return {Function}
   */
  wrapEvent(node, eventName, channel, connection) {
    const { AFTER_PUBLISH, NAV_REQUEST, ROUTER_BACKSTEP, TRACK_EVENT, LOG_EVENT } = externalEventsCodes;

    var source = Observable.fromEvent(node, eventName);
    var wrappedListener = source.subscribe(function(event) {
      if(!this._isEventAtTarget(event)) {
        // If the event bubbles up from a child element:
        return;
      }

      channel.next(event);
      eventManager.emit(AFTER_PUBLISH, event);

      if(connection && connection.link) {

        let linkObject = connection.link;

        if(connection.link.page){
          if(connection.link.page.hasOwnProperty('bind')){
            linkObject.page = event.detail[connection.link.page.bind]
          }
        }

        eventManager.emit(NAV_REQUEST, {
          event: event,
          detail: linkObject
        });
      }

      if (connection && connection.backStep) {
        eventManager.emit(ROUTER_BACKSTEP, {
          event: event,
          detail: {}
        });
      }

      if(connection && connection.analytics) {
        eventManager.emit(TRACK_EVENT, {
          event: event,
          detail: connection.analytics
        });
      }

      if(connection && connection.log) {
        eventManager.emit(LOG_EVENT, {
          event: event,
          detail: connection.log
        });
      }

      if (this.wrapEventHandler) {
        this.wrapEventHandler(event, connection, channel);
      }
    }.bind(this));

    wrappedListener.node = node;
    wrappedListener.eventName = eventName;
    wrappedListener.channelName = channel.name;

    return wrappedListener;
  }

  /**
   * receive a channel name and change old private values.
   *
   * @param  {string} name
   *
   */
  createEvent(name, value) {
    let evt = new Event(name);
    evt.detail = { value };
    return evt;
  }

  /**
   * returns true if there's a private channel with the given name
   *
   * @param {String} name
   *
   * @return {Boolean}
   */
  isActiveBridgeChannel(name) {
    return this.isBridgeChannel(name) && this.manager.getUnsafe(name);
  }

  /**
   * returns true if the given name matches a private channel's name
   *
   * @param {String} name
   *
   * @return {Boolean}
   */
  isBridgeChannel(name) {
    return this.bridgeChannelsPrefix.test(name);
  }

  /**
   * returns true if the subscriptor has been subscribed to the given channel.
   *
   * @param {Subscriptor} subscriptor
   * @param {String} channelName
   *
   * @return {Boolean}
   */
  hasSubscriptions(subscriptor, channelName) {
    return subscriptor.subscriptions.filter(d => d.channel.name === channelName).length > 0;
  }
}

/**
 * ComponentConnector definition
 */
export default ComponentConnector;



