import CellsManagerTemplate from '../../core/manager/template';
import PolymerTemplate from '../template';

export default class PolymerTemplateManager extends CellsManagerTemplate {
  _createCellsTemplate(name, spec) {
    const cellsTemplate = new PolymerTemplate(spec);
    const cellsTemplateConfig = this._createCellsTemplateConfig(name);

    cellsTemplate.config(cellsTemplateConfig);
    return cellsTemplate;
  }

  _createCellsTemplateConfig(name) {
    return {
      name,
      template: {
        id: this.parseTemplateId(name),
        name: this.parseTemplateName(name)
      }
    };
  }
}



